.product--section {
  width: 100%;
  display: flex;
  justify-content: center;
}

.products--list {
  margin-top: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.product--item {
  margin-right: 48px;
  margin-bottom: 50px;
}

.product--item:nth-child(4n) {
  margin-right: 0;
}

@media screen and (min-width: 1200px) {
  .products--list {
    max-width: 1000px;
  }
}
